
export default {
  props: {
    value: {
      type: String,
      default: null,
    },
  },
  data() {
    return {
      initialLanguage: this.value,
    };
  },
  computed: {
    languageInfos() {
      return this.$store.getters['apisettings/settings'].languages.filter(l => l.enabled || l.code === this.initialLanguage);
    }
  }
}
